form{
    width: 90%;
    max-width: 300px;
    background-color: white;
    padding: 33px 44px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

label{
    text-align: center;
    color: black;
    font-weight: bold;
    font-size: 20px;
    margin-bottom: 26px;

}

textarea{
    background: rgb(255, 255, 255);
    border: 2px solid black;
    border-radius: 4px;
    box-shadow: 1px 5px 50px rgba(0, 0, 0, 0.356);
    font-size: 20px;
    text-align: center;
    padding: 12px;
    height: 96px;
    width: calc(100% - 25px);
}

textarea:focus{
    outline-color: aqua;
}

.TodoForm-buttonContainer{
    margin-top: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

.TodoForm-button {
    margin-top: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    cursor: pointer;
    display: inline-block;
    font-size: 20px;
    color: rgb(24, 42, 90);
    font-weight: 400;
    width: 120px;
    height: 40px;
    border-radius: 2px;
    border: none;
}

.TodoForm-button--add{
    background: rgb(193, 155, 255);
    color: white;
    box-shadow: 1px 3px 25px rgb(201, 156, 238) ;
}

.TodoForm-button--cancel{
    background: transparent;
}
